import classNames from 'classnames';
import React, { useState } from 'react';
import { formInputStylesDark, formInputStylesLight } from '../../styles/common-styles';

export interface TextAreaProps extends React.TextareaHTMLAttributes<HTMLTextAreaElement> {
  readonly bottomText?: string;
  readonly color?: 'dark' | 'light';
  readonly textareaClassName?: string;
}

export const TextArea: React.FC<TextAreaProps> = ({
  className,
  textareaClassName,
  bottomText = '',
  color = 'dark',
  children,
  ...props
}) => {
  const [isFocused, setIsFocused] = useState<boolean>(false);
  return (
    <div
      className={classNames(
        'w-full flex-1 flex flex-col',
        {
          ring: isFocused,
        },
        color === 'dark' ? formInputStylesDark : formInputStylesLight,
        className
      )}
    >
      <textarea
        onFocus={() => setIsFocused(true)}
        onBlur={() => setIsFocused(false)}
        className={classNames('flex-1 w-full bg-transparent focus:outline-none', textareaClassName)}
        {...props}
      >
        {children}
      </textarea>
      <span className="text-dimmed2">{bottomText}</span>
    </div>
  );
};
